<template>
		<view class="register">
			<view class="uni-form-item">
				账号：<input class="uni-input" @input="onKeyUser" focus placeholder="请输入账号" />
				邮箱：<input class="uni-input" @input="onKeyEmil"  type="text" placeholder="请输入邮箱" />
				密码：<input class="uni-input" @input="onKeyPsd" password placeholder="请输入密码" />
				手机号：<input class="uni-input" @input="onKeyPho"  type="text" placeholder="请输入手机号" />
				<view class="">
					<button>看不清换一张</button>
					<input type="text">
				</view>
				验证码：<input class="uni-input" placeholder="请输入验证码" />
			</view>
			<view >
				<button class="onregister" @click.native="getRegister">注册</button>
			</view>   
		</view>

</template>

<script>
	import {getRegister} from '@/api/register.js'
	export default {
		data() {
			return {
				// 随机生成验证码
				verification:666666,
				// 验证验证码
				inputVer: 666666,
				// 账号
				inputUser:'',
				// 邮箱
				inputEmil:'',
				// 手机号
				inputPho:'',
				// 密码
				inputPsd:'',
				
			};
		},
		methods:{
			onKeyUser: function(event) {
				this.inputUser = event.target.value
			},
			onKeyPsd: function(event) {
				this.inputPsd = event.target.value
			},
			onKeyPho: function(event) {
				this.inputPho = event.target.value
			},
			onKeyEmil: function(event) {
				this.inputEmil = event.target.value
			},
			getRegister(){
				let register = {
					username: this.inputUser,
					password: this.inputPsd,
					email:this.inputEmil,
					VerificationCode:this.inputVer
				}
				getRegister(register).then(data=>{
					console.log(data);
				})
			}
		}
	}
</script>

<style lang="scss">
	.register {
		width: 400px;
		background-color: aliceblue;
		margin: 0px auto;
		.uni-form-item {
			width: 90%;
			padding: 4%;
			.uni-input {
				width: 100%;
				margin-top: 20px;
				border-bottom: 1px solid #333;
			}
		}
		.onregister{
			margin-top: 20px;
			padding: 12px;
			width: 50%;
			background-color: #3697fd;
			color: white;
		}
	}
</style>
